import {useState} from 'react';

import marketplace from './marketplace1.jpg';
import boost from './boost.jpg';
import zet from './zet.jpg';
import indicorp from './indicorp.jpg';
import digi from './digi.jpg';
import pulse from './pulse.jpg';

import './projectsPage.scss';

const ProjectsPage = () => {
    const [show, setShow] = useState("");

    return (
        <article onLoad={() => {
            setShow("show");
        }} className={`projects ${show}`}>
            <h2 className="projects__title">Проекты</h2>
            <ul className="projects__list">
                <li className="projects__item">
                    <a target="_blank" rel="noreferrer" href="../../projects/marketplace/index.html">
                        <img src={marketplace} alt=""/>
                    </a>
                </li>
                <li className="projects__item">
                    <a target="_blank" rel="noreferrer" href="../../projects/boost/index.html">
                        <img src={boost} alt=""/>
                    </a>
                </li>
                <li className="projects__item">
                    <a target="_blank" rel="noreferrer" href="../../projects/zet/index.html">
                        <img src={zet} alt=""/>
                    </a>
                </li>
                <li className="projects__item">
                    <a target="_blank" rel="noreferrer" href="../../projects/incorp/index.html">
                        <img src={indicorp} alt=""/>
                    </a>
                </li>
                <li className="projects__item">
                    <a target="_blank" rel="noreferrer" href="../../projects/digi/index.html">
                        <img src={digi} alt=""/>
                    </a>
                </li>
                <li className="projects__item">
                    <a target="_blank" rel="noreferrer" href="../../projects/pulse/index.html">
                        <img src={pulse} alt=""/>
                    </a>
                </li>
                <li className="projects__item">
                    <a target="_blank" rel="noreferrer" href="../../projects/zonex/index.html">
                        <img src={pulse} alt="zonex"/>
                    </a>
                </li>
                <li className="projects__item">
                    <a target="_blank" rel="noreferrer" href="../../projects/brella/index.html">
                        <img src={pulse} alt="brella"/>
                    </a>
                </li>
                <li className="projects__item">
                    <a target="_blank" rel="noreferrer" href="../../projects/pollar/index.html">
                        <img src={pulse} alt="pollar"/>
                    </a>
                </li>
            </ul>
        </article>
    );
};

export default ProjectsPage;