import {useState} from 'react';
import './skills.scss';

import htmlSvg from './icons/html-5.svg';
import cssSvg from './icons/css-3.svg';
import jsSvg from './icons/js.svg';
import reactSvg from './icons/react.svg';
import jquerySvg from './icons/jquery.svg';
import webpackSvg from './icons/webpack.svg';
import gulpSvg from './icons/gulp.svg';
import githubSvg from './icons/github.svg';
import bootstrapSvg from './icons/bootstrap.svg';
import sassSvg from './icons/sass.svg';
import postCssSvg from './icons/postCss.svg';
import pugSvg from './icons/pug.svg';
import psSvg from './icons/ps.svg';
import xdSvg from './icons/XD.svg';
import figmaSvg from './icons/figma.svg';

const SkillsPage = () => {
    const [show, setShow] = useState("");

    return (
        <section onLoad={() => {
            setShow("show");
        }} className={`skills ${show}`}>
            <div className="skills__wrapper">
                <h2 className="skills__title">Мои навыки</h2>
                <ul className="skills__list">
                    <li className="skills__item">
                        <img className="skills__img" src={htmlSvg} alt="" />
                        <h3 className="skills__skill-title">HTML</h3>
                    </li>
                    <li className="skills__item">
                        <img className="skills__img" src={cssSvg} alt="" />
                        <h3 className="skills__skill-title">CSS</h3>
                    </li>
                    <li className="skills__item">
                        <img className="skills__img" src={jsSvg} alt="" />
                        <h3 className="skills__skill-title">JavaScript</h3>
                    </li>
                    <li className="skills__item">
                        <img className="skills__img" src={reactSvg} alt="" />
                        <h3 className="skills__skill-title">React.js</h3>
                    </li>
                    <li className="skills__item">
                        <img className="skills__img" src={jquerySvg} alt="" />
                        <h3 className="skills__skill-title">jQuery</h3>
                    </li>
                    <li className="skills__item">
                        <img className="skills__img" src={webpackSvg} alt="" />
                        <h3 className="skills__skill-title">Webpack</h3>
                    </li>
                    <li className="skills__item">
                        <img className="skills__img" src={gulpSvg} alt="" />
                        <h3 className="skills__skill-title">gulp</h3>
                    </li>
                    <li className="skills__item">
                        <img className="skills__img" src={githubSvg} alt="" />
                        <h3 className="skills__skill-title">Github</h3>
                    </li>
                    <li className="skills__item">
                        <img className="skills__img" src={bootstrapSvg} alt="" />
                        <h3 className="skills__skill-title">Bootstrap</h3>
                    </li>
                    <li className="skills__item">
                        <img className="skills__img" src={sassSvg} alt="" />
                        <h3 className="skills__skill-title">Sass</h3>
                    </li>
                    <li className="skills__item">
                        <img className="skills__img" src={postCssSvg} alt="" />
                        <h3 className="skills__skill-title">PostCSS</h3>
                    </li>
                    <li className="skills__item">
                        <img className="skills__img" src={pugSvg} alt="" />
                        <h3 className="skills__skill-title">Pug</h3>
                    </li>
                    <li className="skills__item">
                        <img className="skills__img" src={psSvg} alt="" />
                        <h3 className="skills__skill-title">Photoshop</h3>
                    </li>
                    <li className="skills__item">
                        <img className="skills__img" src={xdSvg} alt="" />
                        <h3 className="skills__skill-title">AdobeXd</h3>
                    </li>
                    <li className="skills__item">
                        <img className="skills__img" src={figmaSvg} alt="" />
                        <h3 className="skills__skill-title">Figma</h3>
                    </li>
                </ul>
            </div>
        </section>
    )
};

export default SkillsPage;