import {Route, Switch} from 'react-router-dom';

import {HomePage, SkillsPage, ProjectsPage} from '../pages';
import Sidebar from '../sidebar';

import './app.scss';

function App() {
    return (
        <div className="app">
            <Sidebar/>
            <Switch>
                <Route exact path="/">
                    <HomePage />
                </Route>
                <Route path="/skills/">
                    <SkillsPage />
                </Route>
                <Route path="/projects/">
                    <ProjectsPage/>
                </Route>
            </Switch>
        </div>
    );
}

export default App;
